	module.exports = {"data":{"site":{"siteMetadata":{"title":"Andrew Lomax"}},"markdownRemark":{"html":"<p>I’m partly a designer, partly a creative, mostly a software engineer, and entirely a problem solver.</p>\n<p>I’ve been working as a technologist at nonprofits in DC for 7 years, which is to say, I’ve been learning the art of doing a lot with a little for 7 years. Little money, little time, small teams. So I’ve learned to be resourceful, empathetic, and efficient.</p>\n<p>The part of my jobs that I have loved the most are the bits where you’re getting to know an organization’s processes, how people think, what they like, and what irks them. Then putting the right piece of code in just the right place to ease some burden. And hopefully, after it’s all said and done, we all smile and laugh and forget that life was ever any other way.</p>\n<p>—</p>\n<p>I enjoy irony, but I often times miss sincerity. I’m aspiring to build software that insists on the dignity and infinite potential of all people. Seriously.</p>"}},"pathContext":{}}

